.banner {
  padding: 120px 0 114px;
  color: $default-color;

  box-sizing: border-box;

  @media (max-width: $desktop) {
    padding: 100px 0;
  }

  @media (max-width: $table) {
    padding: 70px 0;
    max-height: 100%;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    max-height: 506px;
    overflow: hidden;

    @media (max-width: $table) {
      max-height: 100%;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    padding: 84px 78px;
    width: 43%;
    background-color: $secondary-color;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      padding: 50px 45px;
    }

    @media (max-width: $table) {
      width: 100%;
    }

    & p {
      margin: 0;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 150%;

      @media (max-width: $desktop) {
        font-size: 12px;
        line-height: 150%;
      }
    }
  }

  &__pic {
    width: 57%;

    @media (max-width: $table) {
      width: 100%;
    }

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__header {
    display: flex;
  }

  & h2 {
    font-weight: bold;
    font-size: 90px;
    line-height: 90px;
    letter-spacing: 0.03em;
    color: inherit;

    @media (max-width: $desktop) {
      font-size: 52px;
      line-height: 52px;
    }
  }

  &__link {
    display: block;
    margin-top: 46px;
    padding: 23px 50px;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;

    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: inherit;
    text-align: center;
    text-decoration: none;
    border: 4px solid #ffffff;

    @media (max-width: $desktop) {
      font-size: 10px;
      line-height: 13px;
    }
  }
}
